import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";

import { Menu, Home, About } from "./components";

export default function App() {
  // const match = "";
  const match = "/camportfolio";

  return (
    <Router>
      <div id="warpper">
        <Menu match={match} />

        <Route exact path={match + "/"} component={Home} />
        <Route exact path={match + "/about"} component={About} />
        <Route exact path={match + "/works"} component={Home} />
        <Route exact path={match + "/contact"} component={About} />
      </div>
    </Router>
  );
}
