import React from "react";
import { Link } from "react-router-dom";

class ItemMenu extends React.Component {
  render() {
    const { link, name, funcClick, active } = this.props;

    const classLi = active ? "nav-item current" : "nav-item";

    return (
      <li className={classLi}>
        <Link to={link} className="nav-link" onClick={funcClick}>
          {name}
        </Link>
      </li>
    );
  }
}

export default class Menu extends React.Component {
  state = {
    showMenu: false,
    tabActive: ""
  };

  toggleMenu = tabActive => {
    const menu = document.querySelector(".menu");
    const menuBtn = document.querySelector(".menu-btn");
    const menuNav = document.querySelector(".menu-nav");
    const menuBranding = document.querySelector(".menu-branding");
    const navItems = document.querySelectorAll(".nav-item");

    if (!this.state.showMenu) {
      menuBtn.classList.add("close");
      menu.classList.add("show");
      menuNav.classList.add("show");
      menuBranding.classList.add("show");
      navItems.forEach(item => item.classList.add("show"));

      //Set menu state
      this.setState({ showMenu: true });
    } else {
      menuBtn.classList.remove("close");
      menu.classList.remove("show");
      menuNav.classList.remove("show");
      menuBranding.classList.remove("show");
      navItems.forEach(item => item.classList.remove("show"));

      //Set menu state
      this.setState({
        showMenu: false,
        tabActive: tabActive !== "closeMenu" ? tabActive : this.state.tabActive
      });
    }
  };

  render() {
    const { match } = this.props;
    return (
      <header>
        <div className="menu-btn" onClick={() => this.toggleMenu("closeMenu")}>
          <div className="btn-line" />
          <div className="btn-line" />
          <div className="btn-line" />
        </div>

        <nav className="menu">
          <div className="menu-branding">
            <div className="portrait" />
          </div>

          <ul className="menu-nav">
            <ItemMenu
              link={`${match}/`}
              name="Home"
              funcClick={() => this.toggleMenu("")}
              active={this.state.tabActive === "" ? true : false}
            />

            <ItemMenu
              link={`${match}/about`}
              name="About Me"
              funcClick={() => this.toggleMenu("about")}
              active={this.state.tabActive === "about" ? true : false}
            />

            <ItemMenu
              link={`${match}/works`}
              name="My Work"
              funcClick={() => this.toggleMenu("works")}
              active={this.state.tabActive === "works" ? true : false}
            />

            <ItemMenu
              link={`${match}/contact`}
              name="How To Reach Me"
              funcClick={() => this.toggleMenu("contact")}
              active={this.state.tabActive === "contact" ? true : false}
            />
          </ul>
        </nav>
      </header>
    );
  }
}
